<template>
  <header
    class="fixed w-full top-0 z-50 transition-all duration-300"
    :class="
      mobileMenuOpen
        ? 'bg-yuktrix-black/96 backdrop-blur-xl border-b border-white/10'
        : isScrolled
        ? 'bg-yuktrix-black/80 backdrop-blur-md border-b border-white/10'
        : 'bg-transparent'
    "
  >
    <div class="container mx-auto px-4 py-4">
      <div class="flex items-center justify-between">
        <!-- Logo Section (Left) -->
        <NuxtLink to="/" class="flex items-center group">
          <div
            class="md:w-[102px] md:h-[102px] w-[72px] h-[72px] rounded-full flex items-center justify-center overflow-hidden bg-black border border-[#FEE337] shadow-lg"
          >
            <img
              src="/logo.png"
              alt="Yuktrix Logo"
              class="w-full h-full bg-white object-contain"
              @error="$event.target.style.display = 'none'"
            />
          </div>
        </NuxtLink>

        <!-- Navigation & CTA (Right) -->
        <div class="flex items-center space-x-8">
          <!-- Navigation -->
          <nav class="hidden md:flex gap-7 items-center">
            <NuxtLink
              v-for="link in links"
              :key="link.path"
              :to="link.path"
              class="text-base font-medium text-gray-300 hover:text-[#FEE337] transition-colors relative px-1 py-0.5"
              active-class="text-[#FEE337]"
            >
              {{ link.name }}
              <span
                v-if="$route.path === link.path"
                class="block mx-auto mt-1 h-1 w-5 rounded bg-[#FEE337]"
              />
            </NuxtLink>
          </nav>

          <!-- CTA Button -->
          <button
            class="bg-[#FEE337] text-black px-4 py-2.5 rounded-full font-bold text-sm hover:bg-[#FEE337]Hover transition-all hover:scale-105 whitespace-nowrap shadow-md hidden md:block"
          >
            Get Early Access
          </button>

          <!-- Mobile Menu Button -->
          <button
            class="md:hidden text-white hover:text-[#FEE337] transition-colors p-2"
            @click="toggleMobileMenu"
          >
            <svg
              class="w-7 h-7"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>

    <!-- Mobile Menu (Dropdown) -->
    <div
      v-if="mobileMenuOpen"
      class="md:hidden absolute w-full bg-white border-t border-white/10"
    >
      <nav class="flex flex-col p-8 gap-4">
        <NuxtLink
          v-for="link in links"
          :key="link.path"
          :to="link.path"
          class="text-black text-lg font-medium hover:text-[#FEE337] transition-colors py-2"
          active-class="text-[#FEE337]"
          @click="mobileMenuOpen = false"
        >
          {{ link.name }}
        </NuxtLink>
        <button
          class="bg-[#FEE337] text-black px-4 py-3 rounded-full font-bold text-base hover:bg-[#FEE337]Hover transition-all w-full mt-2 shadow"
        >
          Get Early Access
        </button>
      </nav>
    </div>
  </header>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import { useRoute } from "vue-router";

const links = [
  { name: "Home", path: "/" },
  { name: "About", path: "/about" },
  { name: "Services", path: "/services" },
  { name: "Tools", path: "/tools" },
  { name: "Blog", path: "/blog" },
  // { name: "Contact", path: "/contact" },
];

const isScrolled = ref(false);
const mobileMenuOpen = ref(false);

const handleScroll = () => {
  isScrolled.value = window.scrollY > 20;
};

const toggleMobileMenu = () => {
  mobileMenuOpen.value = !mobileMenuOpen.value;
};

onMounted(() => {
  window.addEventListener("scroll", handleScroll);
});

onUnmounted(() => {
  window.removeEventListener("scroll", handleScroll);
});

const $route = useRoute();
</script>
